import { useState, useEffect } from "react";
import Pagination from "../Pagination";
import styles from "../css/List.module.css";
import { Link, Navigate, useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import Category from "../component/Category";
import axios from "axios";
import Footer from "../component/Footer";

function Tech(props) {
  const [posts, setPosts] = useState([]);
  const [limit, setLimit] = useState(4);
  const [page, setPage] = useState(1);
  const offset = (page - 1) * limit;
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const [찜하기, set찜하기] = useState("찜완료❤️");
  const [찜완료, set찜완료] = useState("찜완료❤️");
  let [hart, setHart] = useState(["💙"]);
  let [wish, setWish] = useState(["❤️"]);
  hart.push("💙");
  wish.push("❤️");

  useEffect(() => {
    axios
      .get(
        "https://raw.githubusercontent.com/mdab4793/shop/main/Tech/Tech.json"
      )
      .then((result) => {
        setPosts(result.data);
      });
  }, []);

  return (
    <body className={styles.container}>
      <main className={styles.main}>
        {posts.slice(offset, offset + limit).map(({ id, title, price }) => {
          return (
            <section className={styles.section} key={id}>
              <div className={styles.wrapper}>
                <Link to={`/detail/${id}`}>
                  <img src={posts[id]?.url} alt="" />
                </Link>
                <h3>{posts[id]?.title}</h3>
                <div>
                  <p>${posts[id]?.price}</p>
                </div>
              </div>
              <br />
              <span
                onClick={
                  () => {
                    let copy = [...hart];
                    copy[id] = wish[id];
                    hart.push("💙");
                    setHart(copy);
                    let copy1 = [...wish];
                    copy1[id] = hart[id];
                    wish.push("❤️");
                    setWish(copy1);
                    // setHart("찜완료❤️ ");
                    // setWish("찜하기💙");
                  }
                  // set찜하기(!찜하기);
                  // set찜완료(!찜완료);
                }
              >
                {hart[id]}

                {/* {찜하기 === 1 ? <>찜하기💙</> : null}
                  {찜완료 === 1 ? <>찜완료❤️ </> : null} */}
                {/* {hart === true ? <>찜하기💙</> : null}
                  {wish === true ? <>찜완료❤️ </> : null} */}
                {/* {hart === true ? <>찜하기💙</> : null}
                  {wish === true ? <>찜완료❤️ </> : null} */}
              </span>
            </section>
          );
        })}
      </main>
      )
      <div>
        <Pagination
          total={posts.length}
          limit={limit}
          page={page}
          setPage={setPage}
        />
      </div>
      <footer className={styles.footer}>
        <Footer />
      </footer>
    </body>
  );
}

export default Tech;
